.container {
  display: flex;
}
.container .card {
  padding: 10px;
  background-color: #808080;
}
@media screen and (min-width: 320px) and (max-width: 650px) {
  .container .card:first-child .card {
    background-color: teal;
  }
  .container .card:first-child .card__image {
    width: 150px;
    height: auto;
  }
}
.container .card:first-child .card__actions {
  display: flex;
  justify-content: center;
}
.container .card:first-child .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #41e657;
  color: "initial";
}
.container .card:nth-child(2) .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #800080;
  color: #fff;
}
.container .card:last-child .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #ff0000;
  color: #fff;
}